import React,{useEffect,Fragment}from 'react'
import "./Productlist.css";
import {DataGrid} from '@material-ui/data-grid';
import {useSelector,useDispatch} from 'react-redux'

import {Link} from 'react-router-dom'
import {useAlert} from 'react-alert'
import {Botton, Button} from '@material-ui/core'
import MetaData from '../layout/MetaData'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'
import SideBar from './Sidebar';
import { useNavigate } from 'react-router-dom';
import { DELETE_PRODUCT_RESET } from '../../constants/productConstant';
import { getAllUsers ,clearErrors, deleteUser} from '../../action/userAction';
import { DELETE_USER_RESET } from '../../constants/userConstant';

const UsersList = () => {

  const dispatch =useDispatch();
const navigate=useNavigate();
  const alert =useAlert();
  const { error,users,loading} =useSelector((state)=>state.allUsers);

const {error:deleteError,isDeleted,message} =useSelector((state)=>state.profile);
  const deleteUserHandler=(id)=>{
    dispatch(deleteUser(id))
  }
  

  useEffect(()=>{
    if(error){
      alert.error(error);
      dispatch(clearErrors())
    }
    if(deleteError){
      alert.error(deleteError);
      dispatch(clearErrors())
    }

    if(isDeleted){
      alert.success(message);
    navigate('/admin/users');
 dispatch({type:DELETE_USER_RESET})
    }
    dispatch(getAllUsers());
  },[error,alert,dispatch,isDeleted,deleteError,navigate,message])
const columns=[
  {field:"id",headerName:"User ID",minWidth:180,flex:0.5},

  {field:"email",headerName:"Email",minWidth:150,flex:.7},

  {field:"name",headerName:"Name" ,minWidth:150,flex:0.5},

  {field:"role",headerName:"Role",minWidth:180,flex:0.3,

  cellClassName:(params)=>{

    return params.getValue(params.id,"role")==="admin"?"greenColor":"redColor";
  }


},
  {field:"action",headerName:"Actions",sortable:false, minWidth:150,flex:0.3
  
   ,renderCell:(params)=>{
    return <Fragment>
      <Link to={`/admin/user/${params.getValue(params.id,"id")}`}>
      <EditIcon/>
      </Link>
      <Button>
        <DeleteIcon onClick={()=>deleteUserHandler(params.getValue(params.id,"id"))}/>
      </Button>
    </Fragment>
   }

},
  
];

const rows=[];
users&&
users.forEach((item) => {
  rows.push({
    id:item._id,
    role:item.role,
    name:item.name,
    email:item.email
  })
  
});

  return <Fragment>
    <MetaData title={`ALL USERS - Admin`}/>

    <div className='dashboard'>
      <SideBar/>
      <div className='productListCntainer'>
        <h1 id="productListHeading">ALL USERS</h1>

        <DataGrid
         rows={rows}
         columns={columns}
         pageSize={10}
         disableSelectionOnClick
         className='productListTable'
         autoHeight
        />

      </div>

    </div>
  </Fragment>

}

export default UsersList