import React, { useEffect, Fragment } from 'react'
import "./Productlist.css";
import { DataGrid } from '@material-ui/data-grid';
import { useSelector, useDispatch } from 'react-redux'

import { Link } from 'react-router-dom'
import { useAlert } from 'react-alert'
import { Botton, Button } from '@material-ui/core'
import MetaData from '../layout/MetaData'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'
import SideBar from './Sidebar';
import { useNavigate } from 'react-router-dom';

import { deleteOrder, getAllOrders,clearErrors } from '../../action/orderAction'
import { DELETE_ORDER_RESET } from '../../constants/orderConstant';
const OrderList = () => {

    const dispatch = useDispatch();
    const navigate = useNavigate();
    const alert = useAlert();
    const { error, orders } = useSelector((state) => state.allOrders);

    const { error: deleteError, isDeleted } = useSelector((state) => state.order);
    const deleteOredrHandler = (id) => {
         dispatch(deleteOrder(id))
    }


    useEffect(() => {
        if (error) {
            alert.error(error);
            dispatch(clearErrors())
        }
            if(deleteError){
              alert.error(deleteError);
              dispatch(clearErrors())
            }

            if(isDeleted){
              alert.success("Order Delete Successfully");
         navigate('/admin/orders');
         dispatch({type:DELETE_ORDER_RESET})
            }
        dispatch(getAllOrders());
    }, [error, alert, dispatch, isDeleted, deleteError, navigate])
    const columns = [
        { field: 'id', headerName: "Order ID", minWidth: 200, flex: .7, },
        {
            field: 'name', headerName: "Name", minWidth: 150, flex: 0.3, sortable: false,

        },
        {
            field: 'status', headerName: "Status", minWidth: 150, flex: .5,

            cellClassName: (params) => {
                return params.getValue(params.id, "status") === "Delivered"
                    ? "greenColor" : "redColor"
            }
        },

        { field: 'itemsQty', headerName: "Items Qty", type: "number", minWidth: 150, flex: 0.4 },

        { field: 'amount', headerName: "Amount", type: "number", minWidth: 270, flex: 0.5 },
        {
            field: "action", headerName: "Actions", type: "number", sortable: false, minWidth: 150, flex: 0.3

            , renderCell: (params) => {
                return <Fragment>
                    <Link to={`/admin/order/${params.getValue(params.id, "id")}`}>
                        <EditIcon />
                    </Link>
                    <Button>
                        <DeleteIcon onClick={() => deleteOredrHandler(params.getValue(params.id, "id"))} />
                    </Button>
                </Fragment>
            }

        },

    ];

    const rows = [];
    orders &&
        orders.forEach((item) => {
            rows.push({
                id: item._id,
                itemsQty: item.orderItems.length,
                amount: item.totalPrice,
                status: item.orderStatus
            })

        });

    return <Fragment>
        <MetaData title={`ALL ORDERS - Admin`} />

        <div className='dashboard'>
            <SideBar />
            <div className='productListCntainer'>
                <h1 id="productListHeading">ALL PRODUCTS</h1>

                <DataGrid
                    rows={rows}
                    columns={columns}
                    pageSize={10}
                    disableSelectionOnClick
                    className='productListTable'
                    autoHeight
                />

            </div>

        </div>
    </Fragment>

}

export default OrderList