import React,{useEffect,Fragment}from 'react'
import "./Productlist.css";
import {DataGrid} from '@material-ui/data-grid';
import {useSelector,useDispatch} from 'react-redux'
import {clearErrors,getAdminProduct,deleteProduct} from '../../action/productAction'
import {Link} from 'react-router-dom'
import {useAlert} from 'react-alert'
import {Botton, Button} from '@material-ui/core'
import MetaData from '../layout/MetaData'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'
import SideBar from './Sidebar';
import { useNavigate } from 'react-router-dom';
import { DELETE_PRODUCT_RESET } from '../../constants/productConstant';

const ProductList = () => {

  const dispatch =useDispatch();
const navigate=useNavigate();
  const alert =useAlert();
  const { error,products} =useSelector((state)=>state.products);

const {error:deleteError,isDeleted} =useSelector((state)=>state.product);
  const deleteProductHandler=(id)=>{
    dispatch(deleteProduct(id))
  }
  

  useEffect(()=>{
    if(error){
      alert.error(error);
      dispatch(clearErrors())
    }
    if(deleteError){
      alert.error(deleteError);
      dispatch(clearErrors())
    }

    if(isDeleted){
      alert.success("Product Delete Successfully");
 navigate('/admin/dashboard');
 dispatch({type:DELETE_PRODUCT_RESET})
    }
    dispatch(getAdminProduct());
  },[error,alert,dispatch,isDeleted,deleteError,navigate])
const columns=[
  {field:"id",headerName:"Product ID",minWidth:200,flex:0.5},

  {field:"name",headerName:"Name",minWidth:350,flex:1},

  {field:"stock",headerName:"Stock", type:"number" ,minWidth:150,flex:0.3},

  {field:"price",headerName:"Price",type:"number",minWidth:270,flex:0.5},
  {field:"action",headerName:"Actions", type:"number",sortable:false, minWidth:150,flex:0.3
  
   ,renderCell:(params)=>{
    return <Fragment>
      <Link to={`/admin/product/${params.getValue(params.id,"id")}`}>
      <EditIcon/>
      </Link>
      <Button>
        <DeleteIcon onClick={()=>deleteProductHandler(params.getValue(params.id,"id"))}/>
      </Button>
    </Fragment>
   }

},
  
];

const rows=[];
products&&
products.forEach((item) => {
  rows.push({
    id:item._id,
    stock:item.Stock,
    price:item.price,
    name:item.name
  })
  
});

  return <Fragment>
    <MetaData title={`ALL PRODUCTS - Admin`}/>

    <div className='dashboard'>
      <SideBar/>
      <div className='productListCntainer'>
        <h1 id="productListHeading">ALL PRODUCTS</h1>

        <DataGrid
         rows={rows}
         columns={columns}
         pageSize={10}
         disableSelectionOnClick
         className='productListTable'
         autoHeight
        />

      </div>

    </div>
  </Fragment>

}

export default ProductList